<template>
  <v-container>
    <div v-if="currentStage" class="edit-form py-3">
      <v-row>
        <v-col class="text-center" cols="12" md="4">
          <v-row class="pa-5">
            <h2>EDIT STAGE</h2>
          </v-row>
        </v-col>

        <v-col cols="12" md="4">
          <v-spacer />
          <v-btn
            to="/addStage"
            class="text-decoration-none"
            color="primary"
            dark
            block
            small
            >ADD STAGE</v-btn
          >
        </v-col>

        <v-col cols="12" md="4">
          <v-spacer />
          <v-btn
            to="/stages"
            class="text-decoration-none"
            color="primary"
            dark
            block
            small
            >STAGE LIST</v-btn
          >
        </v-col>
      </v-row>

      <v-form ref="form" lazy-validation>
        <v-text-field
          v-model="currentStage.name"
          :rules="[(v) => !!v || 'Name is required']"
          label="Name"
          required
        ></v-text-field>

        <v-text-field
          v-model="currentStage.stageCode"
          :rules="[(v) => !!v || 'Code is required']"
          label="Code"
          required
        ></v-text-field>

        <v-btn color="error" small class="mr-2" @click="deleteStage">
          Delete
        </v-btn>

        <v-btn color="success" small @click="updateStage"> Update </v-btn>
      </v-form>

      <p class="mt-3">{{ message }}</p>
    </div>

    <div v-else>
      <p>Please click on a Stage...</p>
    </div>
  </v-container>
</template>

<script>
import StageDataService from "../../services/StageDataService";

export default {
  name: "stage",
  data() {
    return {
      currentStage: null,
      message: "",
    };
  },
  methods: {
    getStage(id) {
      StageDataService.get(id)
        .then((response) => {
          this.currentStage = response.data;
          // console.log(response.data);
        })
        .catch((e) => {
          console.log(e);
        });
    },

    updateStage() {
      StageDataService.update(this.currentStage.id, this.currentStage)
        .then((response) => {
          // console.log(response.data);
          this.$store.dispatch("setSnackbar", {
            showing: true,
            text:
              (response && response.data && response.data.message) ||
              toString(),
          });
          this.$router.push({ name: "stages" });
          // this.message = "The Stage was updated successfully!";
        })
        .catch((e) => {
          console.log(e);
        });
    },

    deleteStage() {
      StageDataService.delete(this.currentStage.id)
        .then((response) => {
          // console.log(response.data);
          this.$store.dispatch("setSnackbar", {
            showing: true,
            text:
              (response && response.data && response.data.message) ||
              toString(),
          });
          this.$store.dispatch("setSnackbar", {
            showing: true,
            text:
              (response && response.data && response.data.message) ||
              toString(),
          });
          this.$router.push({ name: "stages" });
        })
        .catch((e) => {
          console.log(e);
        });
    },
  },
  mounted() {
    this.message = "";
    this.getStage(this.$route.params.id);
  },
};
</script>
